import React from 'react';

import styled from 'styled-components';

import artistMain from '../../../assets/logo.svg';
import adele from '../../../assets/adele-wallpaper.png';

const VideoDetailStyled = styled.section`
  display: flex;
  width: 100%;
  height: 250px;
  .artist-figure {
    background: url(${artistMain}) center no-repeat;
    position: relative;
    width: 26%;
    height: 100%;

    i {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 63px;
      color: var(--white);
      cursor: pointer;
    }
  }
  .artist-description {
    background: url(${adele});
    width: 74%;

    padding: 0.8rem 1.5rem;

    &__title {
      margin-top: 1.2rem;
      color: var(--white);
      font-size: 22px;
    }
    &__item {
      color: var(--white);
      p {
        span {
          padding-left: 1rem;
          font-size: 12px;
          color: var(--dark-red-second);
        }
      }
      summary {
        font-size: 13.5px;
        line-height: 20px;
        padding-top: 1rem;
        /* margin: 1rem 0 2rem; */
      }
    }
    .artist-btns {
      /* border: 1px solid red; */
      width: 100%;
      margin-top: 3rem;
      .btn {
        width: 120px;
        font-size: 14px;
        padding: 0.6rem 1rem;
        margin-right: 1rem;
        border-radius: 100px;
        border: 1px solid var(--main-red);
        color: var(--white);
        background: var(--main-red);
        cursor: pointer;
        &.seguir {
          background: transparent;
          color: var(--main-red);
        }
      }
    }
  }
`;

export const VideoDetail = () => {
  return (
    <VideoDetailStyled className="mb-3">
      <div className="artist-figure">
        <i className="fas fa-play"></i>
      </div>
      <div className="artist-description">
        {/* <div> */}
        <h2 className="artist-description__title">Adele 21</h2>
        <div className="artist-description__item">
          <p>
            Lo mejor de Adele <span>321, 123 seguidores</span>
          </p>
          <summary>
            Adele Laurie Blue Adkins (Tottenham, Londres, Inglaterra, 5 de mayo
            de 1988), conocida simplemente como Adele, es una cantante,
            compositora y multinstrumentista británica.8​{' '}
          </summary>
        </div>
        <div className="artist-btns">
          <button className="btn">Reproducir</button>
          <button className="btn seguir">Seguir</button>
        </div>
        {/* </div> */}
      </div>
    </VideoDetailStyled>
  );
};
