import React from 'react';
import styled from 'styled-components';

import { ImageAll } from '../../../helpers/ImageAll';

const SongStyles = styled.div`
  /* border: 1px solid green; */
  .figure {
    position: relative;
    width: 160px;
    height: 160px;
    i {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 2.5rem;
      color: var(--white);
    }
  }
`;

export const Song = ({ figure, song, artist }) => {
  return (
    <SongStyles>
      <div
        style={{
          background: `url(${
            ImageAll(`./${figure}.png`).default
          }) center no-repeat`,
        }}
        className="figure"
      >
        <i className="fas fa-play"></i>
      </div>
      <h4 className="song">{song}</h4>
      <p className="artist">{artist}</p>
    </SongStyles>
  );
};
