import React from 'react';

import styled from 'styled-components';

import logo from '../../assets/foxbel-music.png';

const AsideStyled = styled.aside`
  background: var(--dark-red-second);
  color: var(--white);
  display: flex;
  flex-direction: column;
  align-items: center;
  /* width: 330px; */
  /* width: 30%; */
  /* height: 88vh; */
  height: 100%;
  /* position: absolute; */
  img {
    /* border: 1px solid blue; */
    width: 250px;
    height: 60px;
    margin: 2rem 0;
  }
  .list {
    width: 100%;
    text-align: left;
    margin-bottom: 2rem;
    &__title {
      padding: 0.5rem 2rem;
      font-size: 22px;
      font-weight: bold;
    }
    &__item {
      font-size: 16px;
      padding: 0.4rem 2rem;
      font-weight: 400;
      color: var(--white);
      cursor: pointer;
      transition: color 0.3s ease;
      &.active {
        border-left: 5px solid var(--main-red);
        color: var(--main-red);
      }
      &:hover {
        color: var(--main-red);
      }
    }
  }
`;

export const Aside = () => {
  return (
    <AsideStyled>
      <img src={logo} alt="foxbel-music" loading="lazy" />
      <div className="list">
        <h3 className="list__title">Mi Librería</h3>
        <p className="list__item active">Recientes</p>
        <p className="list__item">Artistas</p>
        <p className="list__item">Álbums</p>
        <p className="list__item">Canciones</p>
        <p className="list__item">Estaciones</p>
      </div>
      <div className="list">
        <h3 className="list__title">Playlist</h3>
        <p className="list__item">Metal</p>
        <p className="list__item">Para bailar</p>
        <p className="list__item">Rock 90s</p>
        <p className="list__item">Baladas</p>
      </div>
    </AsideStyled>
  );
};
