import React from 'react';
import styled from 'styled-components';

import imageSelected from '../../assets/image-1.png';

const FooterStyled = styled.footer`
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100px;
  background: var(--footer-color);
  box-shadow: inset 0px -5px 37px rgba(0, 0, 0, 0.2);
  filter: drop-shadow(0px -5px 37px rgba(0, 0, 0, 0.2));
  .figure {
    display: flex;

    img {
      width: 100px;
      height: 100px;
      object-fit: cover;
    }
    &__description {
      /* border: 1px solid red; */
      margin-left: 1rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
      color: var(--white);
      p {
        font-weight: 700;
        font-size: 14px;
      }
      span {
        display: inline-flex;
        font-weight: 400;
        font-size: 12px;
      }
    }
  }
  /* padding: 0 2rem; */
`;

export const Footer = () => {
  return (
    <FooterStyled>
      <div className="figure">
        <img src={imageSelected} alt="" loading="lazy" />
        <div className="figure__description">
          <p>Canción</p>
          <span>Artist - Albúm</span>
        </div>
      </div>
    </FooterStyled>
  );
};
