import React from 'react';

import styled from 'styled-components';

const SearchStyled = styled.div`
  width: 100%;
  height: 36px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border: 1px solid blue; */
  .bar-search {
    /* border: 1px solid green; */
    line-height: 36px;
    width: 60%;
    position: relative;

    input {
      padding: 0.5rem 1.2rem;
      width: 100%;
      border: 1px solid var(--soft-gray);
      border-radius: 100px;
      font-size: 20px;
      font-weight: 100;
      &::placeholder {
        color: var(--soft-gray);
      }
    }
    i {
      position: absolute;
      right: 18px;
      top: 10px;
      bottom: 10px;
      color: var(--soft-gray);
      font-size: 20px;
      cursor: pointer;
    }
  }
  .user {
    /* border: 1px solid green; */
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 30%;
    i {
      color: var(--main-red);
      padding-right: 0.5rem;
    }
    p {
      font-size: 16px;
    }
  }
`;

export const Search = () => {
  return (
    <SearchStyled className="mb-2">
      <div className="bar-search">
        <i className="fas fa-search"></i>
        <input type="text" placeholder="Buscar..." />
      </div>
      <div className="user">
        <i className="fas fa-user"></i>
        <p>Francisco M.</p>
      </div>
    </SearchStyled>
  );
};
